  <div class="home">
    <el-dialog title="确认登出!"
               :visible.sync="logoutDialogVisible"
               center
               width="250px"
               :show-close="false"
               :close-on-click-modal="false">
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="logoutDialogVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="_logout">确 定</el-button>
      </span>
    </el-dialog>

    <div class="header">
      <el-button icon="el-icon-s-management"
                 class="left-item item"
                 @click="_goHome">声乐</el-button>
      <el-button class="logout-item item"
                 v-show="showLogoutBtn"
                 @click="logoutDialogVisible = true">登出</el-button>
      <el-button icon="el-icon-s-custom"
                 class="right-item item"
                 @click="_clickAccount">admin</el-button>
    </div>

    <el-container class="container"
                  direction="vertical">

      <el-menu :collapse="collapse"
               router
               :default-active="activeIndex">
        <el-menu-item v-for="(item, index) in sideData"
                      :key="index+''"
                      :route="{path: item.path}"
                      :index="item.path">
          <span class="menu-title">{{item.title}}</span>
        </el-menu-item>

      </el-menu>

      <el-main>
        <keep-alive>
          <router-view ref="workList" />
        </keep-alive>
      </el-main>

      <el-pagination background
                     layout="prev, pager, next"
                     :total="1000">
      </el-pagination>
    </el-container>
    <organization></organization>
  </div>

</template>

<script>
import { sideData } from "@/../mock/home_mock"
import Organization from "@/components/Organization.vue"
import { ckGetToken, ckDelToken } from "@/utils/storeUtil/cookieUtil"


export default {
  components: { Organization },
  data () {
    return {
      sideData: sideData,
      collapse: false,
      showLogoutBtn: ckGetToken() != null,
      logoutDialogVisible: false,

    }
  },
  methods: {
    _goHome () {
      if (this.$route.path == "/work") return;
      this.$router.push({ path: "/" })
    },
    _clickAccount () {
      console.log("_clickAccount")
    },
    _logout () {
      this.logoutDialogVisible = false
      ckDelToken()
      this.$router.push({ path: "/login" })
    },

  },
  computed: {
    activeIndex: function () {
      return this.$route.path;
    }
  }
}
</script>

